
.clock {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;

    .digitalClock {
        color: white;
        text-align: center;
        margin: 20px;
        p {
            font-weight: 900;
            > span {
                display: block;
                margin-bottom: 5px;
            }
        }
    }

    .analogClock {
        position: relative;
        width: 100px;
        height: 100px;
        border: 5px solid #ffffff;
        border-radius: 100%;

        .hour {
            position: absolute;
            bottom: calc(50% - 1px);
            left: calc(50% - 1px);
            height: 30px;
            width: 3px;
            background-color: #000000;
            transform-origin: bottom center;
            transition: all 1s;
        }
        .minute {
            position: absolute;
            bottom: calc(50% - 1px);
            left: calc(50% - 1px);
            height: 40px;
            width: 2px;
            background-color: #000000;
            transform-origin: bottom center;
            transition: all 1s;
        }
        .second {
            position: absolute;
            bottom: 50%;
            left: 50%;
            height: 43px;
            width: 1px;
            background-color: #ffffff;
            transform-origin: bottom center;
            transition: all 1s;
        }

        .pin {
            position: absolute;
            bottom: calc(50% - 2px);
            left: calc(50% - 2px);
            width: 4px;
            height: 4px;
            border-radius: 100%;
            background-color: #ffffff;
        }
    }

