                bottom: calc(50%);
                left: calc(50%);
                height: 3px;
                width: 1px;
                background-color: #000000;
                transform-origin: bottom center;
                transition: all 1s;
                transform: rotate(-90deg);
            }
            .m {
                position: absolute;
                bottom: calc(50%);
                left: calc(50%);
                height: 3px;
                width: 1px;
                background-color: #000000;
                transform-origin: bottom center;
                transition: all 1s;
                transform: rotate(90deg);
            }
